import {Banque} from "../../admin/banque/metier/banque.metier";
import {Tiers} from "../../admin/tiers/metier/tiers.metier";
import {Service} from "../../admin/service/metier/service.metier";
import {Utilisateur} from "../../admin/utilisateur/metier/utilisateur.metier";

export class Charge {
  id: number;
  numero: number;
  typePaiement: string;
  date: Date;
  detail: string;
  montant: number;
  banque: Banque;
  tiers: Tiers;
  service: Service;
  numeroCheque: string;
  user: Utilisateur=null;
  dateDebut?: Date;
  dateFin?: Date;

  constructor(values: Object = {}) {
    Object.assign(this, values);
  }
}



