import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { PageTitleService } from '../../core/page-title/page-title.service';
import { fadeInAnimation } from "../../core/route-animation/route.animation";
import { Charge } from "../metier/charge.metier";
import { ChargeService } from "../service/charge.service";
import { BaseComponent } from "../../core/component/base-component";
import { ActivatedRoute, Params } from "@angular/router";
import { Banque } from "../../admin/banque/metier/banque.metier";
import { Tiers } from "../../admin/tiers/metier/tiers.metier";
import { Service } from "../../admin/service/metier/service.metier";
import { BanqueService } from "../../admin/banque/service/banque.service";
import { TiersService } from "../../admin/tiers/service/tiers.service";
import { ServiceService } from "../../admin/service/service/service.service";
import { Utilisateur } from "../../admin/utilisateur/metier/utilisateur.metier";
import { UtilisateurService } from "../../admin/utilisateur/service/utilisateur.service";

@Component({
  selector: 'ms-charge-edit',
  templateUrl: './charge.edit.component.html',
  styleUrls: ['./charge.edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class ChargeEditComponent extends BaseComponent implements OnInit {

  private charge: Charge;
  public chargeForm: FormGroup;
  private typePaiementList: string[] = [];
  private banqueList: Array<Banque> = [];
  private tiersList: Array<Tiers> = [];
  private serviceList: Array<Service> = [];
  private utilisateurList: Array<Utilisateur> = [];
  private typePaiement: string;
  private banque: string;
  private isSuperAdmin: boolean = false;


  constructor(private fb: FormBuilder, private route: ActivatedRoute,
    private pageTitleService: PageTitleService, private chargeService: ChargeService,
    private banqueService: BanqueService, private tiersService: TiersService,
    private serviceService: ServiceService, private utilisateurService: UtilisateurService) {
    super();
  }

  ngOnInit() {
    this.isSuperAdmin = this.compteService.compte.superAdmin;
    this.loadCharge();
    this.pageTitleService.setTitle("MODIFIER UNE CHARGE");
    this.chargeForm = this.fb.group({
      id: [Validators.required],
      numero: ['', Validators.required],
      date: ['', Validators.required],
      tiers: ['', Validators.required],
      service: ['', Validators.required],
      detail: ['', Validators.required],
      montant: ['', Validators.required],
      typePaiement: ['', Validators.required],
      banque: '',
      numeroCheque: '',
      user: null,
    });
  }


  updateCharge(chargeForm: any) {
    this.charge = new Charge(chargeForm);
    this.chargeService.saveCharge(this.charge)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(['/charge/list']);
      }, error => this.showError(error.status, error.json().message))
      ;
  }

  loadCharge() {
    this.route.params.switchMap((params: Params) => this.chargeService.getCharge(+params['id']))
      .subscribe(charge => {
        this.charge = charge;
        this.loadTypePaimentList();
        this.loadTiersList();
        this.loadServiceList();
        this.loadBanqueList();
        this.loadUtilisateurList();
        this.chargeForm.patchValue(this.charge);
      }, error => this.showError(error.status, error.json().message)
      );
  }

  deleteCharge(chargeForm: any) {
    if (!confirm("Etes-vous de vouloir supprimer ces enregistrements !")) {
      return;
    }
    this.charge = new Charge(chargeForm);
    this.chargeService.deleteCharge(this.charge)
      .subscribe(() => {
        this.showInfo("Suppression effectué avec succès");
        this.router.navigate(['/charge/list']);
      }, error => this.showError(error.status, error.json().message))
      ;
  }

  private loadTypePaimentList() {
    this.chargeService.getTypeDePaimentList()
      .subscribe(typePaimentList => {
        this.typePaiementList = typePaimentList;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadBanqueList() {
    this.banqueService.getBanqueList()
      .subscribe(banqueList => {
        this.banqueList = banqueList;
        if (this.charge.banque != null) {
          this.charge.banque = this.banqueList.find(banque => banque.id === this.charge.banque.id);
          this.chargeForm.patchValue({ banque: this.charge.banque }, { onlySelf: true });
        }
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadTiersList() {
    this.tiersService.getTiersList()
      .subscribe(tiersList => {
        this.tiersList = tiersList;
        this.charge.tiers = this.tiersList.find(tiers => tiers.id === this.charge.tiers.id);
        this.chargeForm.patchValue({ tiers: this.charge.tiers }, { onlySelf: true });
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadServiceList() {
    this.serviceService.getServiceList()
      .subscribe(serviceList => {
        this.serviceList = serviceList;
        this.charge.service = this.serviceList.find(service => service.id === this.charge.service.id);
        this.chargeForm.patchValue({ service: this.charge.service }, { onlySelf: true });
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadUtilisateurList() {
    this.utilisateurService.getUtilisateurList()
      .subscribe(utilisateurList => {
        this.utilisateurList = utilisateurList;
        this.charge.user = this.utilisateurList.find(user => user.id === this.charge.user.id);
        this.chargeForm.patchValue({ user: this.charge.user }, { onlySelf: true });
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  checkTypePaiement() {
    this.typePaiement = this.chargeForm.get('typePaiement').value;
    if (this.typePaiement != '' && this.typePaiement != null) {
      if (this.typePaiement == 'ESPECE') {
        this.chargeForm.get("banque").setValue(null);

        return true;
      }
      else {
        return false;
      }
    }
    else {
      return true;
    }
  }


  checkTypeBanque() {
    this.banque = this.chargeForm.get('banque').value;
    if (this.banque != '' && this.banque != null) {
      if (this.typePaiement != '') {

        return false;
      }
      else {
        return true;
      }
    }
    else {
      return true;
    }
  }


  checkBanqueNumCheque() {
    let typePaiement = this.chargeForm.get('typePaiement').value;
    if ((typePaiement == 'CHEQUE' && this.chargeForm.get('banque').value == null) ||
      (typePaiement == 'CHEQUE' && this.chargeForm.get('numeroCheque').value == '')) {
      return false;
    }
    else {
      return true;
    }
  }

}






