"use strict";

import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {PageTitleService} from '../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../core/route-animation/route.animation";
import {Charge} from "../metier/charge.metier";
import {FormBuilder, FormGroup} from "@angular/forms";
import {BaseComponent} from "../../core/component/base-component";
import {ChargeService} from "../service/charge.service";
import {TiersService} from "../../admin/tiers/service/tiers.service";
import {ServiceService} from "../../admin/service/service/service.service";
import {Tiers} from "../../admin/tiers/metier/tiers.metier";
import {Service} from "../../admin/service/metier/service.metier";
import {Utilisateur} from "../../admin/utilisateur/metier/utilisateur.metier";
import {UtilisateurService} from "../../admin/utilisateur/service/utilisateur.service";

@Component({
  selector: 'ms-charge-list',
  templateUrl: './charge.list.component.html',
  styleUrls: ['./charge.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})
export class ChargeListComponent extends BaseComponent implements OnInit {

  public chargeForm: FormGroup;
  private charge: Charge;
  private chargeList: Array<Charge> = [];
  private tiersList: Array<Tiers> = [];
  private serviceList: Array<Service> = [];
  private userList: Array<Utilisateur> = [];
  private isSuperAdmin: boolean = false;

  constructor(private fb: FormBuilder, private chargeService: ChargeService,
              private pageTitleService: PageTitleService,
              private tiersService: TiersService, private serviceService: ServiceService,
              private utilisateurService: UtilisateurService) {
    super();
    this.createForm();

  }

  ngOnInit() {
    this.pageTitleService.setTitle("LISTE DES CHARGES");
    this.isSuperAdmin = this.compteService.compte.superAdmin;
    this.loadChargeList();
    this.loadTiersList();
    this.loadServiceList();
    if (this.isSuperAdmin) {
      this.loadUtilisateurList();
    }
  }

  createForm() {
    this.chargeForm = this.fb.group({
      dateDebut: null,
      dateFin: null,
      service: null,
      tiers: null,
      user: null
    });
  }


  findChargeList(chargeForm: any) {

    this.charge = new Charge(chargeForm);
    this.chargeService.findChargeListByCriteria(this.charge)
      .subscribe(chargeList => {
        this.chargeList = chargeList;

      }, error => {

        this.showError(error.status, error.json().message);
      });
  }

  private loadChargeList() {
    this.chargeService.getChargeList()
      .subscribe(chargeList => {
          this.chargeList = chargeList;
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  private loadServiceList() {
    this.serviceService.getServiceList()
      .subscribe(serviceList => {
          this.serviceList = serviceList;
        }, error => this.showError(error.status, error.json().message)
      );
  }

  private loadTiersList() {
    this.tiersService.getTiersList()
      .subscribe(tiersList => {
          this.tiersList = tiersList;
        }, error => this.showError(error.status, error.json().message)
      );
  }


  private loadUtilisateurList() {
    this.utilisateurService.getUtilisateurList()
      .subscribe(userList => {
          this.userList = userList;
        }, error => this.showError(error.status, error.json().message)
      );
  }

}






