import {Routes} from '@angular/router';

import {ChargeAddComponent}  from './charge.add/charge.add.component';
import {ChargeListComponent} from "./charge.list/charge.list.component";
import {ChargeEditComponent} from "./charge.edit/charge.edit.component";

export const ChargeRoutes:Routes = [{
  path: '',
  redirectTo: 'list',
  pathMatch: 'full',
}, {
  path: '',
  children: [{
    path: 'add', component: ChargeAddComponent},
    {path: 'list', component: ChargeListComponent},
   {path: 'edit/:id', component: ChargeEditComponent},

  ]
}];



