import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FileUploadModule} from 'ng2-file-upload/ng2-file-upload';
import {WizardModule} from 'ng2-archwizard';
import {ChargeAddComponent} from "./charge.add/charge.add.component";
import {ChargeService} from "./service/charge.service";
import {ChargeRoutes} from "./charge.routing";
import {ChargeListComponent} from "./charge.list/charge.list.component";
import {ChargeEditComponent} from "./charge.edit/charge.edit.component";
import {BanqueService} from "../admin/banque/service/banque.service";
import {DataTableModule} from "angular2-datatable";
import {TiersService} from "../admin/tiers/service/tiers.service";
import {ServiceService} from "../admin/service/service/service.service";
import {UtilisateurService} from "../admin/utilisateur/service/utilisateur.service";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    DataTableModule,
    WizardModule,
    RouterModule.forChild(ChargeRoutes)
  ],
  declarations: [
    ChargeAddComponent,
    ChargeListComponent,
    ChargeEditComponent,
  ],
  providers: [
    ChargeService,
    BanqueService,
    TiersService,
    ServiceService,
    UtilisateurService
  ],
})

export class ChargeModule {
}



